* {
 font-family: Arial;
}

body {
  margin-left: auto;
  margin-right: auto;
  max-width: 50%;
}

a {
  color: #9a5b6b;
}

a:visited {
  color: #8a5b9a;
}

hr {
  margin-top: 30px;
  margin-bottom: 30px;
  border: 0;
  border-top: medium double #8A9A5B;
  height: 1px;
  overflow: visible;
  padding: 0;
  color: #8A9A5B;
  text-align: center;
}

hr::after {
  content: "â™¦";
  display: inline-block;
  position: relative;
  top: -0.7em;
  font-size: 1.4em;
  padding: 0 0.3em;
  background: white;
}

.menu {
  background-color: white;
  overflow: hidden;
  margin-top: 30px;
  border: medium double #8A9A5B;
}

.menu a {
  float: left;
  color: #8A9A5B;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.menu a:hover {
  background-color: #6d8133;
  color: white;
}

.menu a:current {
  background-color: #04AA6D;
  color: white;
}